@import "./../../Styles/Mixin_New.scss";

.cteateStory-main-container {
    width: 100%;

    height: 100vh;
    box-sizing: border-box;
    overflow-y: scroll;
    transition: 0.8s ease-in-out;

    @include respond-to(xs) {
        height: calc(100vh - 50px);
    }

    &::-webkit-scrollbar {
        display: none;
    }

    .story-img-object {
        width: 100%;
        max-width: 300px;
        border-radius: 10px;
        height: 100%;
        height: 400px;
        object-fit: cover;
        object-position: center;
        margin-top: 1rem;
    }

    .story-video-object {
        width: 100%;
        max-width: 300px;
        border-radius: 10px;
        height: 100%;
        height: 400px;
        object-fit: cover;
        object-position: center;
        margin-top: 1rem;
    }
}

.background-video {
    position: relative;
    width: 100%;
    height: calc(100vh - 100px);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    .background-div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 1;
        color: white;


        .select-file {
            // width: 180px;
            border: 1px solid #ccc;
            padding: 0.5rem;
            border-radius: 0.5rem;
            background: rgba(0, 0, 0, 0);
            backdrop-filter: blur(7px);

        }
    }
}

.video {
    width: 100%;
    max-height: 400px;
    object-fit: cover;
    object-position: center;
}

.story-mainu {
    padding: 1rem;
    display: flex;
    align-items: center;
    // background: red;
    height: 300px;


    .btn-inline {
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;

    }
}

.story-add-input {
    width: 200px;
}

