.comment-container {
    // padding: 1rem;
    height: auto;
    margin-bottom: 100px;

    .comment-profile-div {
        display: flex;
        align-items: center;

        .comment-profile-img {
            width: 30px;
            height: 30px;
            object-fit: cover;
            object-position: center;
            border-radius: 50%;
        }

        .comment-profile-name {
            margin-left: 0.5rem;
        }
    }

    .comment {
        margin-top: 0.3rem;
        margin-left: 2.5rem;
        text-transform: capitalize;

        .comment-color {
            color: white;
            background-color: #3e4346;
            padding: 0.5rem;
            border-radius: 30px;
        }
    }
}

.comment-text {
    padding: 1rem;
    margin: 1rem 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 35px;
    background: #654EDC;
    text-transform: capitalize;
    color: white;
    font-weight: 600;

    .like {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: end;
    }
}

.timeago {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: end;
    margin-right: 1rem;
}

.folder {
    font-size: 40px;
    margin-right: 1rem;
    margin-bottom: 0.5rem;
    cursor: pointer;
}

.object {
    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover;
    object-position: center;
}