@import "./../Styles/Variable";
@import "./../Styles/Mixin_New.scss";

.wedding-main-div {
    transition: 0.8s ease-in-out;
}

.weddinglist-search-div {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    // background: red;
    margin-top: 1rem;

    @include respond-to(xs) {
        justify-content: flex-end;
    }

    .weddinglist-search {
        // margin-top: 1.5rem;
        width: 85%;
        max-width: 567px;
        height: 40px;
        border-radius: 50px;
        border: none;
        padding-inline-start: 10px;
        box-shadow: $border-input;
        transition: 0.8s ease-in-out;

        &:focus {
            border: none;
            outline: none;
        }
    }
}


.weddinglist-wrapper {
    width: 100%;
    // display: flex;
    // flex-direction: column;
    // align-items: center;

    .weddingList-card-container {
        width: 100%;
        max-width: 567px;
        margin: 0 auto;
        height: 300px;
        // background-color: white;
        box-shadow: $border-input;
        transition: 0.8s ease-in-out;
        border-radius: 10px;

        .weddingList-sender-div {
            width: 100%;
            padding: 1rem;
            display: flex;
            align-items: center;

            .weddingList-sender-photo {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
                margin-right: 1rem;
            }

            .weddingList-delete-div {
                flex: 1;
                display: flex;
                justify-content: end;
                align-items: center;
                padding-right: 0.5rem;
                font-size: 18px;
            }
        }

        .weddingList-profile-div {
            display: flex;
            padding: 0 0.5rem;
            // justify-content: center;

            .weddingList-photo {
                width: 130px;
                height: 180px;
                object-fit: cover;
                object-position: center;
                border-radius: 1rem;
            }

            .weddingList-about-div {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                padding: 0 0.5rem;

                .weddingList-about-inner-div {
                    display: flex;
                    flex-direction: column;
                    height: 140px;
                    align-items: start;
                    justify-content: end;
                    // background: blue;

                    .weddingList-about-inner-item {
                        display: flex;
                        align-items: center;
                        margin-top: 1rem;
                        text-transform: capitalize;
                        flex-wrap: wrap;
                        word-wrap: break-word;
                        display: flex;
                    }
                }
            }

        }

    }
}