.card-container {
    display: flex;
    width: 100%;
    // background: white;
    padding: 0.5rem 0;
    // margin-bottom: 0.5rem;

    .status-card-wrapper {
        border-radius: 5px;
        width: 100%;
        max-width: 510px;
        margin: 0 auto;
        display: inherit;
        box-sizing: border-box;
        overflow-x: scroll;
        scrollbar-width: none;
        -ms-overflow-style: none;
        scroll-behavior: smooth;
        transition: 0.8s ease-in-out;

        &::-webkit-scrollbar {
            display: none;
        }


        .status-video-div {
            width: 180px;
            height: 110px;
            border-radius: 5px;
            position: relative;

            @include respond-to(xs) {
                width: 120px;
                height: 70px;
            }

            .status-video {
                border-radius: 5px;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .story-profile-img {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
                position: absolute;
                top: 0;
                right: 0;
                // border: 2px solid white;
            }

            .new-status-blink {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                position: absolute;
                top: 0;
                right: 0;
                border: 2px solid #2EFEF7;
            }

        }

        .status-card-div {
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            width: 180px;
            height: 110px;
            border-radius: 5px;
            margin: 0 1px;
            position: relative;

            @include respond-to(xs) {
                width: 120px;
                height: 70px;
            }

            .new-status-blink {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                position: absolute;
                top: 0;
                right: 0;
                border: 2px solid #2EFEF7;
            }

            .story-profile-img {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
                position: absolute;
                top: 0;
                right: 0;
            }
        }
    }

    @keyframes blinkStatus {

        0%,
        100% {
            opacity: 30%;

        }

        50% {
            opacity: 100%;
        }
    }

    .c-card {
        width: 140px;
        height: 140px;
        background: blue;
        border-radius: 100%;
        margin: 0.2rem;
        border-radius: 0.5rem;


    }

    .story-div-border {
        border-radius: 50%;
        border: 2px solid #2CB5EC;
    }

    .story-div {
        background-size: cover;
        background-position: center;
        background-position: center;
        background-repeat: no-repeat;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        // background: blue;
        margin: 0.2rem;

        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

        position: relative;

        .story-profile-img {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            object-fit: cover;
            object-position: center;

            // border: 2px solid white;
        }

        .add-story-div {
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.158);
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            border-radius: 50%;

            .add-plus {
                position: absolute;
                bottom: 0;
                font-size: 24px;
                color: white;
            }
        }

    }
}