@import "./../Mixin_New.scss";
@import "./../Variable.scss";
@import "./../Styles/Variable";



.left {
    width: 100%;
    height: 100%;
    max-width: 100px;
    background: red;

    @include respond-to(xs) {
        display: none;
    }
}

.people-wrapper {
    width: 100%;
    height: 100vh;
    // position: fixed;
    top: 0;
    // padding-top: 50px;
    z-index: 9999;

    // background-color: white;
    @include respond-to(xs) {
        position: fixed;
    }

    .people-wrapper-inner {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        // position: relative;

        &::-webkit-scrollbar {
            display: none;
        }

        .People-back-div {
            // position: absolute;
            top: 0;
            // padding: 1rem;
            width: 100%;
            height: 50px;
            // border-bottom: 1px solid #ccc;
            display: flex;
            align-items: center;
            padding: 1rem;

            .bi-arrow-left {
                margin-right: 1rem;
                display: none;

                @include respond-to(md) {
                    display: flex;
                }

                @include respond-to(sm) {
                    display: flex;
                }

                @include respond-to(xs) {
                    display: flex;
                }
            }

            .People-User-input {
                width: 100%;
                max-width: 400px;
                height: 35px;
                padding: 0 10px;
                border-radius: 30px;
                border: none;
                outline: none;
                box-shadow: $border-input;
            }
        }

        .People-user-List {
            padding: 1rem;
            padding-bottom: 2rem;
            color: black;
            display: flex;
            flex-direction: column;
            align-items: center;

            .people-container {
                display: flex;
                align-items: center;
                margin: 1.5rem 0;

                .people-img {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                    object-position: center;
                    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
                }

                .people-name-div {
                    width: 270px;
                    padding-left: 1.5rem;

                    .people-name {
                        font-size: 17px;
                        font-weight: 600;
                        text-transform: capitalize;
                    }

                }

            }
        }

        
    }
}

.hidden {
    display: none !important;
    visibility: hidden !important;
}